#heart {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px;
}

.icon-heart {
  height: 33px;
  width: 70px;
  background-color: rgb(220, 239, 255);
  color: #33a0ff;
  border: none;
  padding: 14px 0 0 125px;
  background-image: url("./hearts.png");
  background-repeat: no-repeat;
  background-position: left 15px bottom 17px;
}
.icon-heart span {
  margin-left: -75px;
  letter-spacing: 0.5px;
}
.icon-heart-small {
  padding: 14px 0 0 14px;
  width: 33px;
}

.icon-heart-small span {
  display: none;
}
.icon-heart:hover {
  background-color: rgb(200, 228, 252);
}

